import { Box } from 'components/Box/Box.styled';
import { useState } from 'react';
import {
  Description,
  DescriptionMoreText,
  DescriptionTrigger,
  DescriptionUnderlineLong,
  DescriptionUnderlineShort,
  HeroSection,
  HeroText,
  HeroTitle,
  HeroTitleWrapper,
} from './Hero.styled';
import { HeroMarquee } from './HeroMarquee/HeroMarquee';

export const Hero = ({ toggleModal }) => {
  const [isMore, setIsMore] = useState(false);

  const showMore = () => {
    setIsMore(isMore => !isMore);
  };

  return (
    <HeroSection id="hero">
      <Box>
        <HeroTitleWrapper>
          <HeroTitle />
          <HeroText>Hot Dog Grooming</HeroText>
        </HeroTitleWrapper>
        <Description>
          <span>
               - ,    
               .    
               ,     ,
                  .
          </span>
          <DescriptionMoreText
            className={isMore ? 'more-shown' : 'more-hidden'}
          >
            {' '}
                ,  , ,
             ,     ,   
            .       ,
                   
            .
          </DescriptionMoreText>
        </Description>
        <DescriptionTrigger onClick={showMore}>
          {isMore ? '' : ' '}
          {isMore ? (
            <DescriptionUnderlineShort />
          ) : (
            <DescriptionUnderlineLong />
          )}
        </DescriptionTrigger>
      </Box>
      <HeroMarquee toggleModal={toggleModal} />
    </HeroSection>
  );
};
